import logo from "./logo.svg";
import "./App.css";
import React, { useState } from "react";

function App() {
  const cal = (n, p, k, w) => {
    const fixN = [46, 0, 0];
    const fixP = [18, 46, 0];
    const fixK = [0, 0, 60];

    const xP = (100 * p) / fixP[1];
    const N_in_P = (n * xP) / 100;
    const xN = (100 * (n - N_in_P)) / fixN[0];
    const xK = (100 * k) / fixK[2];

    const resultN = (w * xN) / 100;
    const resultP = (w * xP) / 100;
    const resultK = (w * xK) / 100;

    return {
      N: resultN.toFixed(2),
      P: resultP.toFixed(2),
      K: resultK.toFixed(2),
    };
  };

  const [calculusN, setCalculusN] = useState("");
  const [calculusP, setCalculusP] = useState("");
  const [calculusK, setCalculusK] = useState("");
  const [calculusW, setCalculusW] = useState("");

  const [calResultN, setCalResultN] = useState("");
  const [calResultP, setCalResultP] = useState("");
  const [calResultK, setCalResultK] = useState("");

  const calculusC = (n, p, k, w) => {
    const calculus = cal(n, p, k, w);
    const N = calculus.N;
    const P = calculus.P;
    const K = calculus.K;

    setCalResultN(N);
    setCalResultP(P);
    setCalResultK(K);
  };

  return (
    <div className="App">
      <div className="boxx">
        <h1> คำนวณหาค่า N,P,K </h1>
        <div className="boxx-item">
          <div className="item-title">
            <h2>N :</h2>
          </div>
          <div className="item-input">
            <input
              type="number"
              className="form-control"
              value={calculusN}
              onChange={(e) => {
                setCalculusN(e.target.value);
              }}
            />
          </div>
        </div>
        <div className="boxx-item">
          <div className="item-title">
            <h2>P :</h2>
          </div>
          <div className="item-input">
            <input
              type="number"
              className="form-control"
              value={calculusP}
              onChange={(e) => {
                setCalculusP(e.target.value);
              }}
            />
          </div>
        </div>
        <div className="boxx-item">
          <div className="item-title">
            <h2>K :</h2>
          </div>
          <div className="item-input">
            <input
              type="number"
              className="form-control"
              value={calculusK}
              onChange={(e) => {
                setCalculusK(e.target.value);
              }}
            />
          </div>
        </div>
        <div className="boxx-item">
          <div className="item-title">
            <h2>W :</h2>
          </div>
          <div className="item-input">
            <input
              type="number"
              className="form-control"
              value={calculusW}
              onChange={(e) => {
                setCalculusW(e.target.value);
              }}
            />
          </div>
        </div>
        <div className="boxx-item">
          <div className="item-title"></div>
          <div className="item-input">
            <button
              type="button"
              class="btn btn-outline-primary btn-cal"
              onClick={() => {
                calculusC(calculusN, calculusP, calculusK, calculusW);
              }}
            >
              คำนวณ
            </button>
          </div>
        </div>
        <div>
          <h4>
            N = {calResultN} , P = {calResultP} , K = {calResultK}
          </h4>
        </div>
      </div>
    </div>
  );
}

export default App;
